import { Component, Input, OnInit } from '@angular/core';
import {
  AbstractControl,
  ControlContainer,
  FormBuilder,
  NgForm,
  ValidationErrors,
  ValidatorFn,
  Validators
} from '@angular/forms';
import { OnBeforeSave } from '@c8y/ngx-components';
import { WidgetConfigComponent } from '@c8y/ngx-components/context-dashboard';
import {
  DatapointAttributesFormConfig,
  DatapointSelectorModalOptions,
  KPIDetails
} from '@c8y/ngx-components/datapoint-selector';
import { IconSelectorService } from '@c8y/ngx-components/icon-selector';
import { Observable } from 'rxjs';
import { KpiWidgetConfig } from '../kpi-widget.model';

export function exactlyASingleDatapointActive(): ValidatorFn {
  return (control: AbstractControl): ValidationErrors | null => {
    const datapoints: KPIDetails[] = control.value;

    if (!datapoints || !datapoints.length) {
      return null;
    }

    const activeDatapoints = datapoints.filter(datapoint => datapoint.__active);

    if (activeDatapoints.length === 1) {
      return null;
    }

    return { exactlyOneDatapointNeedsToBeActive: true };
  };
}

@Component({
  selector: 'c8y-kpi-widget-config',
  templateUrl: './kpi-widget-config.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class KpiWidgetConfigComponent implements OnInit, OnBeforeSave {
  @Input() config: KpiWidgetConfig;
  datapointSelectionConfig: Partial<DatapointSelectorModalOptions> = {};
  defaultFormOptions: Partial<DatapointAttributesFormConfig> = {
    showRedRange: true,
    showYellowRange: true
  };
  formGroup: ReturnType<KpiWidgetConfigComponent['createForm']>;
  availableIcons: string[] = [];
  limits = {
    fontSizeMax: 72,
    fontSizeMin: 18,
    numberOfDecimalPlacesMax: 10,
    numberOfDecimalPlacesMin: 0
  };

  constructor(
    private formBuilder: FormBuilder,
    private form: NgForm,
    private iconSelector: IconSelectorService,
    private widgetConfig: WidgetConfigComponent
  ) {}

  onBeforeSave(config?: KpiWidgetConfig): boolean | Promise<boolean> | Observable<boolean> {
    if (this.formGroup.valid) {
      Object.assign(config, this.formGroup.value);
      return true;
    }
    return false;
  }

  async ngOnInit() {
    if (this.widgetConfig.context?.id) {
      this.datapointSelectionConfig.contextAsset = this.widgetConfig?.context;
    }
    this.initForm();
    if (this.config?.datapoints) {
      this.formGroup.patchValue({ datapoints: this.config.datapoints });
    }
  }

  async openIconSelector() {
    try {
      const icon = await this.iconSelector.selectIcon({
        currentSelection: this.formGroup.value.icon
      });
      this.formGroup.patchValue({ icon });
    } catch {
      // nothing to do
    }
  }

  private initForm(): void {
    this.formGroup = this.createForm();
    this.form.form.addControl('config', this.formGroup);
    this.formGroup.patchValue(this.config);
  }

  private createForm() {
    return this.formBuilder.group({
      numberOfDecimalPlaces: [
        1,
        [
          Validators.required,
          Validators.min(this.limits.numberOfDecimalPlacesMin),
          Validators.max(this.limits.numberOfDecimalPlacesMax)
        ]
      ],
      showTimestamp: [true, []],
      showTrend: [true, []],
      showIcon: [true, []],
      icon: ['water', [Validators.required, Validators.minLength(1)]],
      fontSize: [
        36,
        [
          Validators.required,
          Validators.min(this.limits.fontSizeMin),
          Validators.max(this.limits.fontSizeMax)
        ]
      ],
      datapoints: this.formBuilder.control(new Array<KPIDetails>(), [
        Validators.required,
        Validators.minLength(1),
        exactlyASingleDatapointActive()
      ])
    });
  }
}
