import { Component, Input, OnInit, Optional } from '@angular/core';
import { MeasurementRealtimeService } from '@c8y/ngx-components';
import { KPIDetails } from '@c8y/ngx-components/datapoint-selector';
import { combineLatest, NEVER, Observable } from 'rxjs';
import { distinctUntilChanged, filter, map, pairwise, startWith, tap } from 'rxjs/operators';
import { ContextDashboardComponent } from '@c8y/ngx-components/context-dashboard';
import { KpiWidgetConfig } from '../kpi-widget.model';

interface MeasurementValue {
  unit?: string;
  value: number;
  date: string;
}

enum ColorClass {
  danger = 'text-danger',
  warning = 'text-warning',
  unknown = ''
}

@Component({
  selector: 'c8y-kpi-widget-view',
  templateUrl: './kpi-widget-view.component.html',
  providers: [MeasurementRealtimeService]
})
export class KpiWidgetViewComponent implements OnInit {
  @Input() config: KpiWidgetConfig = { datapoints: [] };
  state$: Observable<{
    latestMeasurement: MeasurementValue;
    previousValue: MeasurementValue;
    trend: string;
    unit: string;
    colorClass: ColorClass;
  }> = NEVER;

  // used to differentiate between loading state and empty state
  noDataInitiallyInDB = false;

  constructor(
    private measurementRealtime: MeasurementRealtimeService,
    @Optional() private dashboard: ContextDashboardComponent
  ) {}

  async ngOnInit() {
    const datapoints = this.config.datapoints || [];
    const datapoint: KPIDetails = datapoints.find(tmp => tmp.__active);
    if (!datapoint) {
      return;
    }

    this.state$ = this.setupObservable(datapoint);
  }

  setupObservable(datapoint: KPIDetails): Observable<{
    latestMeasurement: MeasurementValue;
    previousValue: any;
    trend: string;
    unit: string;
    colorClass: ColorClass;
  }> {
    this.assignContextFromContextDashboard(datapoint);
    const latestMeasurement$ = this.getLatestMeasurement$(datapoint);
    const lastTwoValues$ = this.getLastTwoValuesOfObservable$(latestMeasurement$);

    const previousValue$ = lastTwoValues$.pipe(
      map(res => res[0]),
      startWith(undefined as any)
    );

    const unit$ = latestMeasurement$.pipe(
      map(latestMeasurementValue => datapoint.unit || latestMeasurementValue.unit || ''),
      startWith(''),
      distinctUntilChanged()
    );

    return combineLatest([
      latestMeasurement$,
      previousValue$,
      this.getTrendOfLatestMeasurements$(lastTwoValues$),
      unit$,
      this.getColorClass$(latestMeasurement$, datapoint)
    ]).pipe(
      map(([latestMeasurement, previousValue, trend, unit, colorClass]) => {
        return {
          latestMeasurement,
          previousValue,
          trend,
          unit,
          colorClass
        };
      })
    );
  }

  private getLatestMeasurement$(datapoint: KPIDetails): Observable<MeasurementValue> {
    return this.measurementRealtime
      .latestValueOfSpecificMeasurement$(
        datapoint.fragment,
        datapoint.series,
        datapoint.__target,
        // we only need the last two values in case we want to show a trend
        this.config.showTrend ? 2 : 1,
        // null will be emitted in case no measurement was found initially
        true
      )
      .pipe(
        tap(measurement => {
          if (!measurement) {
            this.noDataInitiallyInDB = true;
          }
        }),
        filter(measurement => !!measurement),
        map(measurement => {
          return {
            unit: measurement[datapoint.fragment][datapoint.series].unit,
            value: measurement[datapoint.fragment][datapoint.series].value,
            date: measurement.time as string
          };
        })
      );
  }

  private getColorClass$(
    measurementAndDatapointCombination$: Observable<MeasurementValue>,
    datapoint: KPIDetails
  ): Observable<ColorClass> {
    return measurementAndDatapointCombination$.pipe(
      map(latestMeasurementValue => {
        if (this.inRangeOf(datapoint, latestMeasurementValue.value, 'redRangeMin', 'redRangeMax')) {
          return ColorClass.danger;
        }

        if (
          this.inRangeOf(
            datapoint,
            latestMeasurementValue.value,
            'yellowRangeMin',
            'yellowRangeMax'
          )
        ) {
          return ColorClass.warning;
        }

        return ColorClass.unknown;
      }),
      startWith(ColorClass.unknown),
      distinctUntilChanged()
    );
  }

  private getLastTwoValuesOfObservable$<T>(input$: Observable<T>): Observable<T[]> {
    return input$.pipe(pairwise());
  }

  private getTrendOfLatestMeasurements$(latestMeasurement$: Observable<MeasurementValue[]>) {
    return latestMeasurement$.pipe(
      map(res => {
        if (res.length === 2) {
          const oldValue = res[0].value;
          const newValue = res[1].value;
          if (oldValue < newValue) {
            return '45deg';
          }
          if (oldValue > newValue) {
            return '135deg';
          }
        }
        return '90deg';
      }),
      startWith('90deg'),
      distinctUntilChanged()
    );
  }

  private inRangeOf(
    datapoint: KPIDetails,
    measurementValue: number,
    minAttribute: string,
    maxAttribute: string
  ): boolean {
    if (
      typeof datapoint[minAttribute] === 'number' &&
      typeof datapoint[maxAttribute] === 'number'
    ) {
      if (
        measurementValue >= datapoint[minAttribute] &&
        measurementValue < datapoint[maxAttribute]
      ) {
        return true;
      }
    }
    return false;
  }

  private assignContextFromContextDashboard(datapoint: KPIDetails) {
    if (!this.dashboard?.isDeviceTypeDashboard) {
      return;
    }
    const context = this.dashboard?.context;
    if (context?.id) {
      const { name, id } = context;
      datapoint.__target = { name, id };
    }
  }
}
